.nav__logo {
  grid-column: 1 / 2;
  align-items: center;
  gap: 32px;
  display: flex;
}

.nav__logo-img {
  transition: all .3s ease-in-out;
}

.nav__logo-img:hover {
  transform: scale(.9);
}

.nav__logo-menu.burger {
  flex-direction: column;
  place-content: center;
  gap: 4px;
  width: 18px;
  height: 18px;
  display: flex;
}

.nav__logo-menu.burger:before {
  content: "";
  background-color: #fff;
  width: 100%;
  height: 2px;
  animation: .5s linear forwards rotate-animation-left-burger;
  display: block;
}

.nav__logo-menu.burger:after {
  content: "";
  background-color: #fff;
  width: 100%;
  height: 2px;
  animation: .5s linear forwards rotate-animation-right-burger;
  display: block;
}

@media (width >= 1024px) {
