    display: none;
  }
}

.nav__logo-menu.close {
  flex-direction: column;
  justify-content: center;
  width: 18px;
  height: 18px;
  display: flex;
}

.nav__logo-menu.close:before {
  content: "";
  background-color: #fff;
  width: 100%;
  height: 2px;
  margin-bottom: -2px;
  animation: .5s linear forwards rotate-animation-left-close;
  display: block;
}

.nav__logo-menu.close:after {
  content: "";
  background-color: #fff;
  width: 100%;
  height: 2px;
  animation: .5s linear forwards rotate-animation-right-close;
  display: block;
}

@media (width >= 1024px) {
